@import url(https://fonts.googleapis.com/css2?family=Overpass:wght@400;500;600;700&family=Tektur&display=swap);
.switch {
    position: relative;
    display: inline-block;
    width: 47px;
    height: 28px;
  }
  
  .switch input { 
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: var(--aquamarine);
    transition: .4s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 20px;
    width: 20px;
    left: 4px;
    bottom: 4px;
    background-color: #020c1b;
    transition: .4s;
  }
  
  input:checked + .slider {
    background-color: var(--aquamarine);
  }
  
  input:focus + .slider {
    box-shadow: 0 0 1px var(--aquamarine);
  }
  
  input:checked + .slider:before {
    transform: translateX(20px);
  }
  
  /* Rounded sliders */
  .slider.round {
    border-radius: 25px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  }

  .language-prefix {
    margin-left: 10px; /* Пример отступа, настройте по вашему усмотрению */
    display: inline-block;
    vertical-align: middle;
    /* margin-top: 5px; */
    line-height: 12px; /* Высота свитчера */
  }
  
.work-section {
  display: flex;
  flex-direction: column;
  grid-gap: 40px;
  gap: 40px;
}
.work__card {
  display: flex;
  flex-direction: row;
  grid-gap: 30px;
  gap: 30px;
  align-items: center;
  border: 1px solid transparent;
  border-radius: 10px;
  transition: all 0.10s ease-in-out;
  padding: 50px;
}
.work__subtitle{
  position: relative;
}
.work__subtitle::after{
  display: block;
  width: calc(var(--index) * 15);
  content: '';
  position: absolute;
  border-bottom: 1px solid var(--bali-hai);
  top: 10%;
  left: 430px;
}
.work__card:hover {
  border-color: var(--rock-blue);
  background-color: rgba(136, 146, 175, 0.15);
}
.card__header {
  font-size: 22px;
  color: var(--rock-blue);
  padding-bottom: 2px;
}
.card__header:hover {
  border-color: var(--rock-blue);
}
.card__photo {
  width: 400px;
  height: 200px;
  border-radius: 10px;
}

.card__info {
  display: flex;
  flex-direction: column;
  grid-gap: 40px;
  gap: 40px;
  text-align: justify;
  color: var(--rock-blue);
}
.card__text {
  font-size: 18px;
  line-height: 1.5em;
}


.link-ease-in-out a {
  display: inline-block;
  text-decoration: none; /* Убираем подчеркивание */
  cursor: pointer;
}
.link-ease-in-out a:after {
  background-color: var(--outline); /* Цвет линии при наведении на нее курсора мыши */
  display: block;
  content: "";
  height: 1px; /* Высота линии */
  width: 0%;
  -webkit-transition: width .3s ease-in-out;
  -moz--transition: width .3s ease-in-out;
  transition: width .3s ease-in-out;
}
.link-ease-in-out a:hover:after,
.link-ease-in-out a:focus:after {
  width: 100%;
}
   
