.element-animation{
    opacity: 0;
    transform: translateY(20px);
}

.element-animation.element-show {
    opacity: 1;
    transition: all 1.5s;
    transform: translateY(0%);
}
p.element-animation:nth-of-type(2).element-show{
    transition-delay: 0.25s;
}
p.element-animation:nth-of-type(3).element-show{
    transition-delay: 0.5s;
}




.about__img.element-animation{
    transform: rotate(-5deg) scale(0.95);
    transition: transform 1.5s;
}

.about__img.element-animation.element-show{
    transform: rotate(0) scale(1);
    transition: transform 1.5s;
}
