@media(min-width: 767px){
    .menu__list{
        display: flex;
        align-items: center;
    }
    .menu__list > li{
        padding: 10px 0;
    }
}

@media(max-width: 767px){
    .menu__icon{
        z-index: 5;
        display: block;
        position: relative;
        width: 30px;
        height: 18px;
        cursor: pointer;
    }
    .menu__icon span,
    .menu__icon::before,
    .menu__icon::after{
        left: 0;
        position: absolute;
        height: 18%;
        width: 100%;
        transition: all .3s ease 0s;
        background-color: #fff;
        border-radius: 2px;
    }
    .menu__icon::before,
    .menu__icon::after{
        content: '';
    }
    .menu__icon::before{
        top: 0;
    }
    .menu__icon::after{
        bottom: 0;
    }
    .menu__icon span{
        top: 50%;
        transform: scale(1) translate(0px, -50%);
    }
    .menu__icon._active span{
        transform: scale(0) translate(0px, -50%)
    }
    .menu__icon._active::before{
        top: 50%;
        transform: rotate(-45deg) translate(-1px, -50%);
    }
    .menu__icon._active::after{
        top: 50%;
        transform: rotate(45deg) translate(-1px, 50%);
    }

    .menu__body{
        position: fixed;
        top: 0;
        left: -100%;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, .9);
        padding: 100px 30px 30px 30px;
        transition: left .3s ease 0s;
    }
    .menu__body._active {
        left: 0;
    }

    .menu__body::before{
        content: '';
        position: fixed;
        width: 100%;
        top: 0;
        left: 0;
        height: 70px;
        z-index: 2;
    }
    .menu__list > li{
        margin: 0px 0px 30px 0px;
    }
    .menu__list > li:last-child{
        margin-bottom: 0;
    }
    .menu__link{
        font-size: 24px;
    }
    .greet-container{
        width: 100%;
    }
}

